(function ($) {
  $(document).ready(function () {
    // Swiper initialize
    var galleryThumbs = new Swiper(".product-media-gallery__thumbs", {
      spaceBetween: 10,
      slidesPerView: 4,
      freeMode: true,
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
    });

    var galleryTop = new Swiper(".product-media-gallery__top", {
      spaceBetween: 10,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
      thumbs: {
        swiper: galleryThumbs,
      },
    });

    // $("[data-badge]").each(function () {
    //   const badgeValue = $(this).data("badge");
    //   $(this).addClass("has-badge");
    //   $(this).append(`<span class="has-badge__item">${badgeValue}</span>`);
    // });
    if ($("body").width() <= "576") {
      if ($(document).scrollTop() >= 300) {
        $(".main-header__top").addClass("fixed");
      } else {
        $(".main-header__top").removeClass("fixed");
      }
    }
    $(document).on("scroll", function () {
      if ($("body").width() <= "576") {
        if ($(document).scrollTop() >= 300) {
          $(".main-header__top").addClass("fixed");
        } else {
          $(".main-header__top").removeClass("fixed");
        }
      }
    });

    $(".categories-panel a").each(function () {
      $(this)[0].ondragstart = function () {
        return false;
      };
    });
    $(".categories-panel").each(function () {
      let $self = $(this);
      let panStart = null;
      let categoryPan = new Hammer($self[0]);

      categoryPan.on("panstart", function ($event) {
        panStart = $event.center.x;
      });
      categoryPan.on("panend", function ($event) {
        panStart = null;
      });

      categoryPan.on("pan", function ($event) {
        const walk = ($event.center.x - panStart) * 0.08; //scroll-fast
        $self.scrollLeft($self.scrollLeft() - walk);
      });
    });

    $(".static-seo__toggle").each(function () {
      $(this).on("click", function (e) {
        e.preventDefault();
        const parent = $(this).closest(".static-seo");
        parent.addClass("active");
      });
    });

    // Open video overlay
    $(
      ".open-video-popup, .blog-posts-grid__video-item .blog-posts-grid__item-thumbnail-wrap a",
    ).on("click", function (e) {
      e.preventDefault();
      $("body").addClass("overflow-hidden");
      $("#videoPopupOverlay").addClass("active");
    });

    // Close overlay on outside click
    $(".overlay-cdk").on("click", function () {
      $(this).removeClass("active");
      $("body").removeClass("overflow-hidden");
    });
    // Prevent overlay closing on content click
    $(".overlay-cdk__content").on("click", function () {
      return false;
    });

    // product reviews bar value
    $(".product-page__reviews-summary-score-bar").each(function () {
      const value = $(this).data("percent-value");
      const percent = Number(value);
      if (percent && !isNaN(percent) && percent >= 0 && percent <= 100) {
        const bar = $(this).find(
          ".product-page__reviews-summary-score-bar-inner",
        );
        bar.css("width", `${percent}%`);
      }
    });
  });
})(jQuery);
